import Loader from "@/components/Loader";
import Title from "@/components/ui/Title";
import { useSpacecraftQuery } from "@/hooks/queries/useSpacecraftQuery";
import Image from "next/image";
import React from "react";
import parse from "html-react-parser";

const RumfaergenPage = () => {
  const { data, isError, isLoading } = useSpacecraftQuery();

  return (
    <main>
      <section className="relative mb-16">
        <figure className="max-h-[31rem] w-full overflow-hidden">
          <Image className="w-full h-auto object-cover" src="/banner-spaceship.jpg" width={2000} height={400} alt="Baggrunds billede for nyhedstilmelding" />
        </figure>
        <div className="absolute top-0 left-0 flex justify-center items-center w-full h-full">
          <Title className="text-7xl text-white font-bold" text="RumfÃ¦rgen" />
        </div>
      </section>
      <section className="flex justify-between mt-60">
        <figure className="w-[47%]">
          <Image src={"/om-os.jpg"} width={1000} height={500} alt="Billede fra rummet for Om Os" />
        </figure>
        <section className="w-[45%] flex flex-col gap-10 my-auto">
          {isLoading && <Loader />}
          {isError && <Error />}
          {data && (
            <>
              <Title className="text-5xl font-bold" text="Lidt om os" />

              <div className="flex flex-wrap gap-y-3 justify-end">
                <Title className="w-full text-lightseagreen uppercase" text={data.title} />
                <div className="w-1/2 h-1 bg-lightseagreen" />
                <div className="w-1/2 h-[0.175rem] mt-auto bg-neutral-200" />
              </div>

              <div className="text-lg">{parse(data.content)}</div>
            </>
          )}
        </section>
      </section>
    </main>
  );
};

export default RumfaergenPage;
